@import "./variables";
/* apply a natural box layout model to all elements, but allowing components to change */
html {
    box-sizing: border-box;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}
html,
body {
    width: 100%;
    height: 100%;
}
body {
    margin: 0;
    font-family: "Open Sans", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.AppContainer {
    width: 60%;
    margin: 0px auto;
    @include breakpoint(xs) {
        width: 90%;
    }
    @include breakpoint(sm) {
        width: 80%;
    }
}
.Heading {
    color: #222;
    @include font-bold(32px);
    text-align: center;
    margin: 32px 16px;
    margin-bottom: 8px;
    margin-top: 5%;
}
.SubHeading {
    color: $textColor_300;
    @include font-normal(20px);
    text-align: center;
}
.Footer {
    background-color: #333648;
    width: 100%;
    padding: 16px 32px;
    text-align: center;
    color: #ffffff;
    @include font-normal(16px);
}
